<script type="text/javascript">
import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
import anime from 'animejs/lib/anime.es.js';
import '@/plugins/classie.js'


export default {
	name: 'entry-info',
	props: {

	},
	data() {
		return {
			focusAppear: false,
			search_input:'',
			slogan_input: '这是推荐文案',
			recommend_doc: [
				'越冬，越要燃',
				'恭喜发财',
				'新年快乐',
				'回春之术',
				'为你所鼠',
				'庚子鼠年新年快乐',
				'鼠兆丰年',
				'新年严禁放嘴炮',
				'期待的未来，其实就在眼前',
			],
			logoImg: '',
			productImg: '',
		}
	},
	computed: {
		...mapState(['questionOriginData', 'author_id']),
		limit: function() {
			// 输入字数显示
			return this.slogan_input.length
		},
		// 筛选1
		filter_doc: function() {
			const _search_input= this.search_input;
			if(_search_input) {
				const reg = new RegExp(_search_input,'ig')
				const _pushArr=[];
				this.recommend_doc.map(function(item) {
					if(item.search(_search_input) != -1){
						const _item=item.replace(_search_input,function(e) {
							return '<strong style="color: #ff5342">'+ e + '</strong>'
						})
						_pushArr.push(_item);
					}
				})
				return _pushArr
			}
			return this.recommend_doc;
		},
	},
	created: function() {
		// const me =this;
		this.busEvent()
	},
	mounted: function() {
		this.initInput()
		
	},
	methods: {
		initInput: function() {
			if (!String.prototype.trim) {
				(function () {
                    // Make sure we trim BOM and NBSP
                    var rtrim = /^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g;
                    String.prototype.trim = function () {
                    	return this.replace(rtrim, '');
                    };
                })();
            }

            [ ].slice.call(document.querySelectorAll('input.input__field')).forEach(function (inputEl) {
                // in case the input is already filled..
                if (inputEl.value.trim() !== '') {
                	classie.add(inputEl.parentNode, 'input--filled');
                }

                // events:
                inputEl.addEventListener('focus', onInputFocus);
                inputEl.addEventListener('blur', onInputBlur);
            });

            function onInputFocus(ev) {
            	classie.add(ev.target.parentNode, 'input--filled');
            }

            function onInputBlur(ev) {
            	if (ev.target.value.trim() === '') {
            		classie.remove(ev.target.parentNode, 'input--filled');
            	}
            }
        },
        open_upload_dia_Func: function(index) {
			const me =this;
			this.openMater('inputMsg', me.author_id, index)
		},
		busEvent: function() {
			const me =this;
			bus.$on('uploadSuccess_logo',function(data) {
				// 上传成功的函数
				me.uploadSuccessFunc_logo({
					pic_id: data.data.file_id,
					pic_path: data.data.savepath
				})
			})
			bus.$on('uploadSuccess_product',function(data) {
				// 上传成功的函数
				me.uploadSuccessFunc_product({
					pic_id: data.data.file_id,
					pic_path: data.data.savepath
				})
			})
		},
		uploadSuccessFunc_logo: function(data) {
			// 更新logo
			console.log(data)
			this.logoImg = data.pic_path
		},
		uploadSuccessFunc_product: function(data) {
			// 更新product
			console.log(data)
			this.productImg = data.pic_path
		},
		changeSlogan: function(doc, index) {
			// 从推荐文案中选择slogan
			this.$set(this, 'slogan_input', doc[index])
		},
		deleteImgFunc: function(type) {
			// 删除当前图片
			if(type == 'logo') {
				this.logoImg = ''
			}else if(type == 'product') {
				this.productImg = ''
			}
		},
		go_design_func: function() {
			// 跳转到列表页、附带着参数
			this.$router.push('/design')
		},
		startAniFunc: function() {
			const me =this;
			me.focusAppear = true
			let t1 = anime.timeline({
				easing: 'cubicBezier(.5, .05, .1, .3)',
				duration: 500
			});
			t1.add({
				targets: '.auto_copy_wrap ',
				translateX: -100+ '%',
			})
			.add({
				targets: '.entry_wrap ',
				translateX: -15+ '%',
				scale: .9
			},'-=450')
			.add({
				targets: '.right_blobk ',
				translateX: 100+ '%',
			},'-=450')
			
			
		},
		endAniFunc: function() {
			let t2 = anime.timeline({
				easing: 'cubicBezier(.5, .05, .1, .3)',
				duration: 500
			});
			t2.add({
				targets: '.entry_wrap ',
				translateX: 0,
				scale: 1
			})
			.add({
				targets: '.auto_copy_wrap ',
				translateX: 0 + '%',
			},'-=450')
			.add({
				targets: '.right_blobk ',
				translateX: 0+ '%',
			},'-=450')
		}
	},
	beforeDestroy: function() {
		this.endAniFunc()
	}
}

</script>