<script type="text/javascript">
import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
import anime from 'animejs/lib/anime.es.js';
export default {
	name: 'q2',
	props: {

	},
	data: function(){
		return {
			state: 'state1',
		}
	},
	computed: {
		...mapState(['questionOriginData']),
		btns: function() {
			return this.questionOriginData[1].btns
		},
	},
	created: function() {
		
	},
	methods: {
		tabChangeFunc: function(index) {
			const me =this;
			const dom = document.getElementById('select_btn');
			const domWd = 184,
					tranX = domWd * index;
			if(index >1 && index < 4){
				me.$set(me, 'state', 'state2')
			}else if(index > 3) {
				me.$set(me,'state', 'state3')
			}else{
				me.$set(me,'state', '')
			}
			anime({
				targets: '.tabDeco',
				easing: 'easeOutElastic(1,1)',
				duration: 1000,
				translateX: tranX/100 + 'rem'
			});
			me.decoChangeFunc(index)
		},
		decoChangeFunc: function(index) {
			anime({
				targets: '.select_pic',
				easing: 'easeInOutQuad',
				duration: 1000,
				translateX: (11*index) + '%'
			});
			anime({
				targets: '.select_pic span',
				easing: 'easeInOutQuad',
				duration: 1000,
				delay: 500,
				translateX: (1.5*index) + '%'
			});
			anime({
				targets: '.mosaic',
				easing: 'easeInOutQuad',
				duration: 1000,
				// delay: 400,
				translateX: -(32.5*index) + '%'
			});
			anime({
				targets: '.deco_block h2',
				easing: 'easeInOutQuad',
				duration: 1000,
				// delay: 500,
				translateX: -(62.5*index) + '%'
			});
			anime({
				targets: '.mouseTag',
				easing: 'easeInOutQuad',
				duration: 500,
				translateX: (20*index) + '%',
			});
		},
	},
	 beforeRouteEnter(to,from,next) {
	 	next(vm => {
	 		vm.tabChangeFunc(vm.curSelectIndex)
	 	})
	 }
}

</script>