import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

Vue.use(Vuex)

const vuexLocal = new VuexPersistence({
    storage: window.sessionStorage
})

export default new Vuex.Store({
  state: {
    answer_index: 1,
    isLogin: Vue.prototype.getCookie('isLogin') ? Vue.prototype.getCookie('isLogin') : false,
    currentUser: Vue.prototype.getCookie('currentUser') ? JSON.parse(Vue.prototype.getCookie('currentUser')).user_name : '小智',
    author_id: Vue.prototype.getCookie('author_id') ? Vue.prototype.getCookie('author_id') : '',
    questionOriginData: [
      {
        quest_title: '服务公司所属行业',
        btns: [
          {id: 0, answer_t: '手机', imgSrc: require('@/assets/answer/phone.png'), select: true},
          {id: 1, answer_t: '化妆品', imgSrc: require('@/assets/answer/makeup.png'), select: false},
          {id: 2, answer_t: '电器', imgSrc: require('@/assets/answer/machine.png'), select: false},
        ],
        selectIndex: 0
      },
      {
        quest_title: '服务公司的品牌特征',
        btns: [
          {id: 0, answer_t: '摄影达人', imgSrc: require('@/assets/answer/pic_1.png'), select: true},
          {id: 1, answer_t: '游戏动漫控', imgSrc: require('@/assets/answer/pic_1.png'), select: false},
          {id: 2, answer_t: '影视音乐爱好者', imgSrc: require('@/assets/answer/pic_1.png'), select: false},
          {id: 3, answer_t: '科技数码达人', imgSrc: require('@/assets/answer/pic_1.png'), select: false},
          {id: 4, answer_t: '美容美妆爱好者', imgSrc: require('@/assets/answer/pic_1.png'), select: false},
        ],
        selectIndex: 0
      },
      {
        quest_title: '此次推广面向的目标人群',
        btns: [
          {id: 0, answer_t: '新潮有趣', imgSrc: require('@/assets/answer/pic_6.png'), select: true},
          {id: 1, answer_t: '专业可靠', imgSrc: require('@/assets/answer/pic_2.png'), select: false},
          {id: 2, answer_t: '狂野豪迈', imgSrc: require('@/assets/answer/pic_3.png'), select: false},
          {id: 3, answer_t: '朴实亲和', imgSrc: require('@/assets/answer/pic_4.png'), select: false},
          {id: 4, answer_t: '高贵优雅', imgSrc: require('@/assets/answer/pic_5.png'), select: false},
        ],
        selectIndex: 0
      },
      {
        quest_title: '此次推广的传播目的',
        btns: [
          {id: 0, answer_t: '促销宣传', select: true},
          {id: 1, answer_t: '新品发布', select: false},
          {id: 2, answer_t: '品牌宣传', select: false},
        ],
        selectIndex: 0
      },
      {
        quest_title: '此次推广的投放渠道',
        btns: [
          {id: 0, answer_t: '开屏页',imgSrc: require('@/assets/answer/img_welcome.png'),sizeSrc: require('@/assets/answer/size_1.png'), select: true},
          {id: 1, answer_t: '信息流',imgSrc: require('@/assets/answer/img_info.png'),sizeSrc: require('@/assets/answer/size_2.png'), select: false},
          {id: 2, answer_t: '网站Banne',imgSrc: require('@/assets/answer/img_banner.png'),sizeSrc: require('@/assets/answer/size_3.png'), select: false},
          {id: 3, answer_t: '电商平台',imgSrc: require('@/assets/answer/img_e.png'),sizeSrc: require('@/assets/answer/size_4.png'), select: false},
        ],
        selectIndex: 0
      }

    ]
  },
  mutations: {
    setAnswerIndex(state, data) {
      // 设置answer_index
      if(data < 1){ data = 1}
        else if(data > 5){data = 5}
      state.answer_index = data
    },
    setQuestionOriginData(state, data) {
      const changeIndex = state.answer_index-1;
      state.questionOriginData[changeIndex].btns = data[0];
      //同时更改选择的答案
      state.questionOriginData[changeIndex].selectIndex = data[1]
    },
    setUserStatus(state, data) {
      if(data) {
        state.isLogin = true;
        state.currentUser = data.user_name;
        state.author_id = data.id
      }else {
        return false
      }
    }
  },
  actions: {},
  plugins: [vuexLocal.plugin]
})
